<template>
  <el-row
    ><el-select v-model="testMethod" placeholder="请选择">
      <el-option
        v-for="item in testOptions"
        :key="item"
        :label="item"
        :value="item"
      >
      </el-option>
    </el-select>
    <el-button type="primary" @click="Test()">测试</el-button>
  </el-row>
</template>

<script>
export default {
  props: ["activePath"],
  data() {
    return {
      //测试方法
      testMethod: "",
      //测试结果
      testResults: {},
    };
  },
  created() {},
  watch: {
    activePath: function (val) {
      console.log(val);
    },
  },
  computed: {
    //测试选择
    testOptions: function () {
      let options = [];
      switch (this.activePath) {
        //三角形问题
        case "/triangle":
          options = ["boundary", "equivalence"];
          break;
        //电脑销售问题
        case "/commission":
          options = ["boundary-input", "boundary-output"];
          break;
        //电信收费问题
        case "/charge":
          options = ["boundary", "equivalence", "decision", "final"];
          break;
        //万年历问题
        case "/calendar":
          options = [
            "boundary",
            "equivalence-weak-general",
            "equivalence-strong-general",
            "equivalence-weak-robust",
            "equivalence-strong-robust",
          ];
          break;
      }
      console.log(options);
      return options;
    },
  },
  methods: {
    //测试
    async Test() {
        let results = {}
      switch (this.activePath) {
        //三角形问题
        case "/triangle":
          results = await this.$http.get(
            "/question1/triangle/" + this.testMethod
          );
          break;
        //电脑销售问题
        case "/commission":
          results = await this.$http.get(
            "/question1/commission/" + this.testMethod
          );
          break;
        //电信收费问题
        case "/charge":
          results = await this.$http.get(
            "/question1/charge/" + this.testMethod
          );
          break;
        //万年历问题
        case "/calendar":
          results = await this.$http.get(
            "/question1/calendar/" + this.testMethod
          );
          break;
      }
      this.testResults = results.data;
      console.log(this.testResults);
    },
  },
};
</script>

<style lang="less" scoped>
.el-select {
  margin-right: 20px;
}
</style>